import React from 'react';

import CountContext from './counter-demo/CountContext';
import './App.css';
import Container from './counter-demo/Container';

class App extends React.Component {
  state = {
    count: 0
  }

  handleIncrementClick = () => {
    this.setState({
      count: this.state.count + 1
    })
  }
  
  handleDecrementClick = () => {
    this.setState({
      count: this.state.count - 1
    })
  }
  render() {
    const contextData = {
      count: this.state.count,
      increment: this.handleIncrementClick,
      decrement: this.handleDecrementClick
    };
    return(
      <div className="App">
        <CountContext.Provider value={ contextData }>
          <Container  />
        </CountContext.Provider>
      </div>
    )
    };
}


export default App;